@import '../styles/basic';

.device{
    position: relative;
    width: 60%;
    max-width: 1200px;
    z-index: 2;
    margin-left: 15%;
    margin-top: 20px;
    border-radius: 8px;
    animation: video2 10s ease-in-out infinite;
    @include xs-width{
        width: 80%;
        margin: 50px auto 0px auto;
    }
    @include sm-width{
        width: 80%;
        margin: 50px auto 0px auto;
    }
    @include md-width{
        width: 80%;
        margin: 50px auto 30px auto;
    }
    @include lg-width{
        width: 70%;
        margin: 50px auto 30px auto;
    }

}

.topBanner2 {
    position: absolute;
    left: 200px;
    top: 12%;
    width: 220px;

    z-index: 3;
    
    color: #4a4a4a;
    transform: translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg);
    transform-style: preserve-3d;
    will-change: transform;
    @include xs-width{
        width: 20%;
        left: 20px;
        top: 36%;
    }
    @include sm-width{
        width: 20%;
        left: 20px;
        top: 30%;
    }
    @include md-width{
        width: 20%;
        left: 50px;
        top: 28%;
    }
    @include lg-width{
        width: 17%;
        left: 10%;
        top: 26%;
    }


}

.video1{
    box-shadow: 0 0 20px rgba(0,0,0,0.5);
    width: 60%;
    @media only screen and (max-width: 600px) {
        width: 100%;
        max-width: 100%;
    }
  }